@import url('https://fonts.googleapis.com/css2?family=Fredoka');

*{
  margin: 0;
  font-family: "Fredoka";
  color: #333;
  
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1 px solid #f2f2f2;
}
.navbar h1{
  color: #228c22;
}
.navbar .links{
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
color: #228c22;
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px; 
}
.recipe-preview{
  padding: 10px 16px;
  margin: 20 px 0;
  border-bottom: 1 px solid #fafafa;
}
.recipe-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.recipe-preview h2{
  font-size: 20px;
  color: #228c22;
  margin-bottom: 8px;
}
.recipe-preview a{
 text-decoration: none;
}
.recipe-details h2{
  font-size: 20px;
  color:#228c22;
  margin-bottom: 10px;
}
.recipe-details div{
  margin: 20px 0;
}
.recipe-details button{
  background: #228c22;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.create{
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label{
  text-align: left;
  display: block;
}
.create h2{
  font-size: 20px;
  color: #228c22;
  margin-bottom: 30px;
}

.create input,.create textarea, .create select{
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button{
  background: #228c22;
  color: #fff;
  border:0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}